import React, { useState, useCallback } from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import { useDropzone } from "react-dropzone";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  Box,
  Button,
  IconButton,
  Link,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Tooltip,
  Typography,
  makeStyles,
} from "@material-ui/core";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import MoreIcon from "@material-ui/icons/MoreVert";
import bytesToSize from "src/utils/bytesToSize";
import { uploadImageRequest } from "src/actions/imageActions";
import { useDispatch } from "react-redux";
const useStyles = makeStyles((theme) => ({
  root: {},
  dropZone: {
    border: `1px dashed ${theme.palette.divider}`,
    padding: theme.spacing(6),
    outline: "none",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap",
    alignItems: "center",
    "&:hover": {
      backgroundColor: theme.palette.action.hover,
      opacity: 0.5,
      cursor: "pointer",
    },
  },
  dragActive: {
    backgroundColor: theme.palette.action.active,
    opacity: 0.5,
  },
  image: {
    width: 130,
  },
  info: {
    marginTop: theme.spacing(1),
  },
  list: {
    maxHeight: 320,
  },
  actions: {
    marginTop: theme.spacing(2),
    display: "flex",
    justifyContent: "flex-end",
    "& > * + *": {
      marginLeft: theme.spacing(2),
    },
  },
}));

function ImageDropzone({ className, setThumbnail, ...rest }) {
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);
  const classes = useStyles();
  const [files, setFiles] = useState([]);
  const uploadImageCallBack = (event) => {
    return new Promise((resolve, reject) => {
      let name = event.name?.replaceAll(" ", "_");
      console.log("uploadImageCallBack", event);

      if (event.size > 5000000) {
        alert("Vui lòng chọn ảnh có dung lượng < 5 mb");
      } else {
        setIsLoading(true);
        let fileData = new FileReader();
        fileData.onloadend = async (e) => {
          const content = e.target.result;
          console.log("file content", content);
          // You can set content in state and show it in render.
          let object = {
            imageName: name,
            imageSoure: content,
          };
          console.log(object);
          var resultUpload = await dispatch(
            uploadImageRequest(object),
            (res) => {},
            (err) => {
              console.log(err);
            }
          );
          console.log("resultUpload", resultUpload.imgePath);
          setThumbnail(resultUpload.imgePath);
          resolve({
            data: { link: resultUpload.imgePath },
          });
        };
        fileData.readAsDataURL(event);
      }
    });
  };
  const handleDrop = useCallback((acceptedFiles) => {
    console.log("ImageDropzone acceptedFiles", acceptedFiles);
    uploadImageCallBack(acceptedFiles[0]);
    setFiles((prevFiles) => [...prevFiles].concat(acceptedFiles));
  }, []);

  const handleRemoveAll = () => {
    setFiles([]);
  };

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop: handleDrop,
  });

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <div
        className={clsx({
          [classes.dropZone]: true,
          [classes.dragActive]: isDragActive,
        })}
        {...getRootProps()}
        style={{ padding: 10 }}
      >
        <input {...getInputProps()} />
        <div>
          <img
            alt="Select file"
            className={classes.image}
            src="/static/images/undraw_add_file2_gvbb.svg"
          />
        </div>
        <div>
          <Typography gutterBottom variant="h3">
            Select files
          </Typography>
          <Box mt={2}>
            <Typography color="textPrimary" variant="body1">
              Drop files here or click <Link underline="always">browse</Link>{" "}
              thorough your machine
            </Typography>
          </Box>
        </div>
      </div>
      {isLoading && <div class="loading"></div>}
    </div>
  );
}

ImageDropzone.propTypes = {
  className: PropTypes.string,
};

export default ImageDropzone;
