import React, { useRef, useEffect } from "react";
import { useDispatch } from "react-redux";
import clsx from "clsx";
import * as Yup from "yup";
import PropTypes from "prop-types";
import { Formik } from "formik";
import {
  Box,
  Button,
  Checkbox,
  FormHelperText,
  TextField,
  Typography,
  Link,
  makeStyles,
  InputAdornment,
  IconButton,
  Grid,
  ListItemText,
  Menu,
  MenuItem,
  FormControlLabel,
  Paper,
  Card,
  CardContent,
  CardHeader,
  Divider,
} from "@material-ui/core";
import { useState } from "react";
import ImageDropzone from "src/components/ImageDropzone";

import {
  EditorState,
  convertToRaw,
  convertFromHTML,
  ContentState,
  convertFromRaw,
} from "draft-js";
import draftToHtml from "draftjs-to-html";
import { editFeedbackRequest } from "src/actions/feedbackActions";

const useStyles = makeStyles(() => ({
  root: {},
}));

function EditForm({ className, onSubmitSuccess, jobDetail, ...rest }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const sortRef = useRef(null);
  const [editorState, setEditorState] = useState(() =>
    EditorState.createEmpty()
  );
  const [thumbnail, setThumbnail] = useState("");
  const [dataDetail, setDataDetail] = useState("");
  useEffect(() => {
    if (jobDetail !== undefined && jobDetail.name !== undefined) {
      setDataDetail(jobDetail);
      setThumbnail(jobDetail.thumbnail);
    }
  }, [jobDetail]);

  return (
    <div>
      {dataDetail !== "" && (
        <Formik
          initialValues={{
            name: dataDetail.name,
            description: dataDetail.description,
            active: 1,
          }}
          validationSchema={Yup.object().shape({
            name: Yup.string()
              .max(255)
              .required("Thông tin bắt buộc"),
          })}
          onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
            try {
              var objet = {
                id: dataDetail._id,
                name: values.name,
                description: values.description,
                thumbnail: thumbnail,
              };
              await dispatch(
                editFeedbackRequest(
                  objet,
                  (res) => {
                    console.log("createBlogRequest", res);
                    setStatus({ success: true });
                    setSubmitting(false);
                    onSubmitSuccess();
                  },
                  (error) => {
                    console.log("errorerror", error);
                  }
                )
              );
            } catch (error) {
              setStatus({ success: false });
              setErrors({ submit: error.message });
              setSubmitting(false);
            }
          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            touched,
            values,
          }) => (
            <form
              className={clsx(classes.root, className)}
              onSubmit={handleSubmit}
              {...rest}
            >
              <Grid container justify="space-between" spacing={3}>
                <Grid item xs={12} lg={6}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label="Tên khu vực"
                    margin="normal"
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    type="name"
                    value={values.name}
                    variant="outlined"
                  />
                  <TextField
                    error={Boolean(touched.description && errors.description)}
                    fullWidth
                    helperText={touched.description && errors.description}
                    label="Giới thiệu tóm tắt"
                    margin="normal"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    type="text"
                    value={values.description}
                    variant="outlined"
                    style={{ marginTop: 30 }}
                    multiline
                    rows={5}
                    name="description"
                  />
                  <Box mt={5}>
                    <Card>
                      <CardHeader title="Ảnh đại diện đối tác" />
                      <Divider />
                      {thumbnail !== "" ? (
                        <CardContent>
                          <div
                            style={{
                              display: "flex",
                              flexDirection: "column",
                              alignItems: "center",
                            }}
                          >
                            <img
                              style={{
                                width: 300,
                                height: 200,
                                objectFit: "cover",
                              }}
                              src={thumbnail}
                            />
                            <Box mt={1}>
                              <Button
                                color="secondary"
                                size="large"
                                variant="contained"
                                onClick={() => {
                                  setThumbnail("");
                                }}
                              >
                                Xoá
                              </Button>
                            </Box>
                          </div>
                        </CardContent>
                      ) : (
                        <CardContent>
                          <ImageDropzone setThumbnail={setThumbnail} />
                        </CardContent>
                      )}
                    </Card>
                  </Box>
                  <Box mt={2}>
                    <Button
                      color="secondary"
                      disabled={isSubmitting}
                      fullWidth
                      size="large"
                      type="submit"
                      variant="contained"
                    >
                      Cập nhật
                    </Button>
                  </Box>
                </Grid>
                <Grid item xs={12} lg={6}></Grid>
              </Grid>
            </form>
          )}
        </Formik>
      )}
    </div>
  );
}

EditForm.propTypes = {
  className: PropTypes.string,
  onSubmitSuccess: PropTypes.func,
};

EditForm.default = {
  onSubmitSuccess: () => {},
};

export default EditForm;
