import React, { useEffect, useState } from "react";
import { Container, makeStyles } from "@material-ui/core";
import Page from "src/components/Page";
import EditForm from "./EditForm";
import { useHistory } from "react-router";
import { useSnackbar } from "notistack";
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: "100%",
    paddingTop: theme.spacing(3),
    paddingBottom: 100,
  },
}));

function EditView(props) {
  const classes = useStyles();
  const history = useHistory();
  const [jobDetail, setSrojectDetail] = useState([]);

  const { enqueueSnackbar } = useSnackbar();
  useEffect(() => {
    console.log("props.location.state", props.location.state);
    if (
      props.location.state !== undefined &&
      props.location.state.pathSEO !== ""
    ) {
      setSrojectDetail(props.location.state);
    }
  }, [window.location.pathname]);
  const handleSubmitSuccess = () => {
    enqueueSnackbar("Cập nhật thành công", {
      variant: "success",
    });
    history.push("/app/management/feedbacks");
  };
  const handleSubmitFailed = () => {
    enqueueSnackbar("Cập nhật thất bại", {
      variant: "error",
    });
  };

  return (
    <Page className={classes.root} title="User Create">
      <Container maxWidth="lg">
        <EditForm
          jobDetail={jobDetail}
          onSubmitSuccess={handleSubmitSuccess}
          onSubmitFailed={handleSubmitFailed}
        />
      </Container>
    </Page>
  );
}

export default EditView;
